import { historyKey } from './storageConsts'

export function getHistoryDetailAtIndex({cofId,runIndex}){
    const COFHistory = getHistoryForCof({cofId})
    return COFHistory?.[runIndex]
}

export function getCourseOfFireLastIndex({cofId}){
    const COFHistory = getHistoryForCof({cofId})
    return COFHistory.length - 1
}

export function getHistoryForCof({cofId}){
    const allHistory = JSON.parse(localStorage.getItem(historyKey) || '{}')
    return allHistory?.[cofId]|| []
}
