import { useEffect, useState } from 'react';
import { useParams, useNavigate } from 'react-router-dom'
import getCurrentCof from '../storage/getCurrentCOF';
import { Fieldset, Input, InputWrapper, Label } from "../components/FormElements";
import { PrimaryButton, SecondaryButton } from "../components/Buttons";
import { setMarksmanshipData } from "../storage/setCurrentCOF";
import { pushCOFToHistory } from '../storage/setHistoryCOF'
import { getCourseOfFireLastIndex } from '../storage/getHistoryCOF';

function translateFormToNumericalValues(uiFormState, hitCountData) {
    return uiFormState.map(({head,targetName,torso})=>{
        return({
            targetName,
            head:{
                a:Number(head.a),
                c:Number(head.c),
            },
            torso:{
                a:Number(torso.a),
                c:Number(torso.c),
                d:Number(torso.d),
            },

        })
    })

}

export default function TotalScore({ getPageData }) {
    const { cofId } = useParams()
    const pageData = getPageData(cofId)
    const { hitCountData } = pageData

    const navigate = useNavigate()
    const { marksmanship, time } = getCurrentCof({ cofId })
    const [uiFormState, setUiFormState] = useState(marksmanship)
    const [formStateWithNumberValues, setFormStateWithNumberValues] = useState(translateFormToNumericalValues(uiFormState, hitCountData))

    useEffect(() => {
        setFormStateWithNumberValues(translateFormToNumericalValues(uiFormState))
    }, [uiFormState])

    useEffect(() => {
    setMarksmanshipData({ cofId, data: formStateWithNumberValues })
    }, [formStateWithNumberValues])

    function onSubmit(e) {
        pushCOFToHistory({ cofId, data: {marksmanship:formStateWithNumberValues, time } })
        const cofHistoryIndex = getCourseOfFireLastIndex({ cofId })
        navigate(`/scoreDetail/${cofId}/${cofHistoryIndex}`)
        e.preventDefault();
    }
    function onChange({ formObj, index, e, key, zone }) {
        setUiFormState(uiFormState.map((arrObj, mapIndex) => index === mapIndex ? { ...formObj, [key]: { ...formObj[key], [zone]: e.target.value } } : arrObj))
    }
    return (
        <main style={{ padding: "1rem 0" }}>
            <h2 style={{ color: '#CEFF51' }}>MARKSMANSHIP</h2>

            <form onSubmit={onSubmit}>
                {uiFormState.map((formObj, i) => (
                    <section key={i}>
                        <h3 style={{ textTransform: 'capitalize' }}>{formObj.targetName} Target</h3>
                        <Fieldset >
                            {/* <legend style={{ textTransform: 'capitalize' }}>Head</legend> */}
                            <InputWrapper>
                                <Label style={{ textTransform: 'capitalize' }} htmlFor={`head-a-${i}`}> head a </Label>
                                <Input
                                    onChange={(e) => { onChange({ formObj, index: i, e, key: 'head', zone: 'a' }) }}
                                    // required
                                    value={formObj.head.a}
                                    type="number"
                                    min="0"
                                    autoComplete="off"
                                    id={`head-a-${i}`}
                                />
                            </InputWrapper>
                            <InputWrapper >
                                <Label style={{ textTransform: 'capitalize' }} htmlFor={`head-c-${i}`}> head c </Label>
                                <Input
                                    onChange={(e) => { onChange({ formObj, index: i, e, key: 'head', zone: 'c' }) }}
                                    // required
                                    value={formObj.head.c}
                                    type="number"
                                    min="0"
                                    autoComplete="off"
                                    id={`head-c-${i}`}
                                />
                            </InputWrapper>
                            <InputWrapper>
                                <Label style={{ textTransform: 'capitalize' }} htmlFor={`torso-a-${i}`}> torso a </Label>
                                <Input
                                    onChange={(e) => { onChange({ formObj, index: i, e, key: 'torso', zone: 'a' }) }}
                                    // required
                                    value={formObj.torso.a}
                                    type="number"
                                    min="0"
                                    autoComplete="off"
                                    id={`torso-a-${i}`}
                                />
                            </InputWrapper>
                            <InputWrapper>
                                <Label style={{ textTransform: 'capitalize' }} htmlFor={`torso-c-${i}`}> torso c </Label>
                                <Input
                                    onChange={(e) => { onChange({ formObj, index: i, e, key: 'torso', zone: 'c' }) }}
                                    // required
                                    value={formObj.torso.c}
                                    type="number"
                                    min="0"
                                    autoComplete="off"
                                    id={`torso-c-${i}`}
                                />
                            </InputWrapper>
                            <InputWrapper>
                                <Label style={{ textTransform: 'capitalize' }} htmlFor={`torso-d-${i}`}> torso d </Label>
                                <Input
                                    onChange={(e) => { onChange({ formObj, index: i, e, key: 'torso', zone: 'd' }) }}
                                    // required
                                    value={formObj.torso.d}
                                    type="number"
                                    min="0"
                                    autoComplete="off"
                                    id={`torso-d-${i}`}
                                />
                            </InputWrapper>

                        </Fieldset>
                    </section>

                ))}

                <SecondaryButton onClick={() => navigate(-1)} type="button">Back</SecondaryButton>
                <PrimaryButton type="submit">Next</PrimaryButton>
            </form>
        </main>
    );
}