import { historyKey } from './storageConsts'

export function pushCOFToHistory({cofId,data}){
    const allHistory = JSON.parse(localStorage.getItem(historyKey) || '{}')
    const COFHistory = allHistory?.[cofId]|| []
    COFHistory.push(data)
    localStorage.setItem(historyKey, JSON.stringify({...allHistory, [cofId]:COFHistory}))
}
